.custom-button {
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px 0 35px;
  font-size: 15px;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed';
  font-weight: bolder;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;

  &:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
  }

  &.google-sign-in {
    background-color: #4285f4;
    color: white;

    &:hover {
      background-color: #357ae8;
      border: none;
    }
  }

  &.inverted {
    background-color: black;
    color: white;
    border: 1px solid black;
    border: 1px solid white;
    &:hover {
      background-color: black;
      color: white;
      transition: all 0.3s;
      // prettier-ignore
      box-shadow:
        0.5px 0.5px white,
        1px 1px white,
        1.5px 1.5px white,
        2px 2px white,
        2.5px 2.5px white,
        3px 3px white,
        3.5px 3.5px white,
        4px 4px white;
    }
  }
}
